import {
    REQUEST_CHANGE_IMAGE_USERS,
    REQUEST_CHANGE_IMAGE_USERS_FAILED,
    REQUEST_CHANGE_IMAGE_USERS_SUCCESS,
    REQUEST_CHANGE_PASSWORD_USERS,
    REQUEST_CHANGE_PASSWORD_USERS_FAILED,
    REQUEST_CHANGE_PASSWORD_USERS_SUCCESS,
    REQUEST_USERS,
    REQUEST_USERS_FAILED,
    REQUEST_USERS_SUCCESS,
} from '../constant/UsersConstant';
import {
    REQUEST_ALL_MEMBERS_BY_USER_ID,
    REQUEST_ALL_MEMBERS_BY_USER_ID_FAILURE,
    REQUEST_ALL_MEMBERS_BY_USER_ID_SUCCESS
} from "../constant/MemberConstatant";
// import validSession from "../../../../utils/ValidSession";
import { apiBaseUrl } from "../apiBaseUrl";
import { errorAlert, getError, getResponseError, successAlert } from "../helper/handlingError";
import checkAuth from "../../auth";

const fetchUsersAction = (id, token) => {
    return async (dispatch) => {
        dispatch({
            type: REQUEST_USERS,
        });
        try {
            const { data, status } = await apiBaseUrl({
                method: 'GET',
                data: {},
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${token}`,
                },
                url: `api/v2/users/${id}`,
            });
            if (data.result == null) {
                dispatch({
                    type: REQUEST_USERS_FAILED,
                });
                return getResponseError(data.responseData.responseMessage);
            }

            if (status === 200) {
                dispatch({
                    type: REQUEST_USERS_SUCCESS,
                    payload: data,
                });
                return data;
            }
            // return errorAlert('token is expired, please login again!!!');
        } catch (error) {
            dispatch({
                type: REQUEST_USERS_FAILED,
            });
            sessionStorage.removeItem('sessionData');
            await getError(error);
        }
    };
};

const fetchAllMemberByUserIdAction = () => {
    return async (dispatch) => {
        dispatch({
            type: REQUEST_ALL_MEMBERS_BY_USER_ID,
        });
        const token = checkAuth();
        const userDetail = JSON.parse(localStorage.getItem('userDetail'));
        try {
            const { data, status } = await apiBaseUrl({
                method: 'GET',
                data: {},
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${token}`,
                },
                url: `/api/v2/users/find-all-member/${userDetail.id}`,
            });
            if (data.result == null) {
                dispatch({
                    type: REQUEST_ALL_MEMBERS_BY_USER_ID_FAILURE,
                });
                return getResponseError(data.responseData.responseMessage);
            }

            if (status === 200) {
                dispatch({
                    type: REQUEST_ALL_MEMBERS_BY_USER_ID_SUCCESS,
                    payload: data,
                });
                return data;
            }
            // return errorAlert('token is expired, please login again!!!');
        } catch (error) {
            dispatch({
                type: REQUEST_ALL_MEMBERS_BY_USER_ID_FAILURE,
            });
            sessionStorage.removeItem('sessionData');
            await getError(error);
        }
    };
};

const fetchChangeImagesAction = (request) => {
    return async (dispatch) => {
        dispatch({
            type: REQUEST_CHANGE_IMAGE_USERS,
        });
        try {
            const token = checkAuth();
            const userDetail = JSON.parse(localStorage.getItem('userDetail'));

            const formData = new FormData();
            formData.append('image', request.image);
            if (token) {
                const { data, status } = await apiBaseUrl({
                    method: 'POST',
                    data: formData,
                    headers: {
                        'Content-Type': 'application/form-data',
                        Authorization: `Bearer ${token}`,
                    },
                    url: `/api/v2/users/change-image/${userDetail.id}`,
                });
                if (data.result == null) {
                    dispatch({
                        type: REQUEST_CHANGE_IMAGE_USERS_FAILED,
                    });
                    return getResponseError(data.responseData.responseMessage);
                }

                if (status === 200) {
                    dispatch({
                        type: REQUEST_CHANGE_IMAGE_USERS_SUCCESS,
                        payload: data.result,
                    });
                    successAlert('Change image success!!!')
                    setTimeout(() => {
                        window.location.reload();
                    }, 3000)
                    return data.result;
                }
            }
            return errorAlert('token is expired, please login again!!!');
        } catch (error) {
            dispatch({
                type: REQUEST_CHANGE_IMAGE_USERS_FAILED,
            });
            await getError(error);
        }
    };
};

const fetchChangePasswordUsersAction = (password) => {
    console.log(password)
    return async (dispatch) => {
        dispatch({
            type: REQUEST_CHANGE_PASSWORD_USERS,
        });
        try {
            const token = checkAuth();
            if (token) {
                const { data, status } = await apiBaseUrl({
                    method: 'POST',
                    data: { password: password },
                    params: {},
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: `Bearer ${token}`,
                    },
                    url: '/api/v2/users/change-password',
                });
                if (data.result == null) {
                    dispatch({
                        type: REQUEST_CHANGE_PASSWORD_USERS_FAILED,
                    });
                    return getResponseError(data.responseData.responseMessage);
                }

                if (status === 200) {
                    dispatch({
                        type: REQUEST_CHANGE_PASSWORD_USERS_SUCCESS,
                        payload: data.result,
                    });
                    successAlert('Change password success!!!')
                    setTimeout(() => {
                        window.location.reload();
                    }, 3000)
                    return data;
                }
            }
            return errorAlert('token is expired, please login again!!!');
        } catch (error) {
            dispatch({
                type: REQUEST_CHANGE_PASSWORD_USERS_FAILED,
            });
            await getError(error);
        }
    };
};

