import { useNavigate } from "react-router-dom";
import React, { useState } from "react";
import logo from "../../assets/image/logo-pbt.png";
import { BottomNavigation, BottomNavigationAction } from "@mui/material";
import {
    EventAvailableRounded,
    HomeRounded,
    MiscellaneousServicesRounded,
    CardGiftcardOutlined
} from "@mui/icons-material";
import AppBar from "@mui/material/AppBar";

export const BottomNavigator = () => {

    const navigate = useNavigate();
    const [value, setValue] = useState(false);

    function handleClick(path) {
        navigate(path)
    }

    return (
        <AppBar position={'sticky'} className={'bottom-navigation'}>
            <BottomNavigation
                showLabels
                value={value}
                onChange={(event, newValue) => {
                    setValue(newValue);
                }}
                style={{
                    position: 'fixed',
                    bottom: 0,
                    left: 0,
                    right: 0,
                    backgroundColor: 'yellow'
                }}
            >
                <BottomNavigationAction label="Beranda"
                    icon={<HomeRounded />}
                    onClick={() => handleClick("/member")} />

                <BottomNavigationAction label="Kegiatan"
                    icon={<EventAvailableRounded />}
                    onClick={() => handleClick("/member/event")} />
                <BottomNavigationAction />

                <div className={'bottom-navigation-icon'} onClick={() => handleClick("/member/member-card")}>
                    <img src={logo} alt="" />
                </div>

                <BottomNavigationAction label="Prestasi"
                    icon={<CardGiftcardOutlined />}
                    onClick={() => handleClick("/member/achievement")} />

                <BottomNavigationAction label="Akun" icon={<MiscellaneousServicesRounded />}
                    onClick={() => handleClick("/member/setting")} />
            </BottomNavigation>
        </AppBar>
    );
}
