import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import React, {useEffect} from "react";
import logo from '../../assets/image/Pushbike tangerang logo.png';
import noImage from '../../assets/image/no-image-icon-33.png';
import "./navbar.css"
import {useNavigate} from "react-router-dom";
import {useDispatch, useSelector} from 'react-redux';
import {fetchUsersAction} from "../../app/service/action/usersAction";
import {LogoutSharp} from "@mui/icons-material"

export const ResponsiveAppBar = ({token, userDetail}: any) => {
    const navigation = useNavigate();
    const dispatch: any = useDispatch();
    const handleProfileMenuOpen = () => {
        navigation('/login')
    };

    const {isLoading, data} = useSelector(({usersState}) => usersState.users);

    useEffect(() => {
        // @ts-ignore
        dispatch(fetchUsersAction(userDetail.id, token));
    }, [dispatch, userDetail.id, token]);

    function logoutUser() {
        localStorage.clear();
        window.location.href = '/'
    }

    return (
        <>
            <AppBar position="sticky" id={'navbar'}>
                <Container maxWidth="xl">
                    <Toolbar disableGutters id={'navbar-content'}>
                        <div id={"logo"}>
                            <img src={logo} alt="logo"/>
                        </div>
                        <Typography
                            variant="h6"
                            noWrap
                            component="a"
                            href="#app-bar-with-responsive-menu"
                            sx={{
                                mr: 2,
                                display: {xs: 'none', md: 'flex'},
                                fontFamily: 'monospace',
                                fontWeight: 700,
                                letterSpacing: '.3rem',
                                color: 'inherit',
                                textDecoration: 'none',
                            }}
                        >
                        </Typography>
                        <Typography
                            variant="h5"
                            noWrap
                            component="a"
                            href="#app-bar-with-responsive-menu"
                            sx={{
                                mr: 2,
                                display: {xs: 'flex', md: 'none'},
                                flexGrow: 1,
                                fontFamily: 'monospace',
                                fontWeight: 700,
                                letterSpacing: '.3rem',
                                color: 'inherit',
                                textDecoration: 'none',
                            }}
                        >
                        </Typography>
                        <Box sx={{flexGrow: 1, display: {xs: 'flex', md: 'flex'}, justifyContent: 'end'}}>
                            {!isLoading ? (
                                <>
                                    <div className="dropdown dropdown-end ml-4">
                                        <label tabIndex={0} className="avatar">
                                            <div id={'image-navbar'}>
                                                <span className={'badge'}>Online</span>
                                                {data.result.image !== 'default.png' ? (
                                                    <img src={data.result.image} alt=""/>
                                                ) : (
                                                    <img src={noImage} alt=""/>
                                                )}
                                            </div>
                                        </label>
                                        <ul tabIndex={0}
                                            className="menu menu-compact dropdown-content mt-3 p-2 shadow bg-base-100 rounded-box w-52">
                                            <div className="divider mt-0 mb-0"></div>
                                            <li className={'text-black'}>
                                                <a href='#' onClick={logoutUser}> <LogoutSharp/> Logout</a>
                                            </li>
                                        </ul>
                                    </div>
                                </>
                            ) : (
                                <Button
                                    sx={{my: 2, color: 'white', display: 'block'}}
                                    variant={'contained'}
                                    color={'primary'}
                                    onClick={handleProfileMenuOpen}
                                >
                                    Login
                                </Button>
                            )}
                        </Box>
                    </Toolbar>
                </Container>
            </AppBar>
        </>
    );
}
