import { lazy } from "react";

const Home = lazy(() => import("../pages/member/Home"));
const Event = lazy(() => import("../pages/member/Event"));
const DetailEvent = lazy(() => import('../pages/member/EventDetail'))
const RegistrationEvent = lazy(() => import('../pages/member/RegistrationEvent'))
const MemberCard = lazy(() => import('../pages/member/MemberCard'))
const Setting = lazy(() => import('../pages/member/Setting'))
const Account = lazy(() => import('../pages/member/Account'))
const Riders = lazy(() => import('../pages/member/Riders'))
const RiderDetail = lazy(() => import('../pages/member/RiderDetail'))
const RegistrationMember = lazy(() => import('../pages/member/RegistrationMember'))
const OrderJersey = lazy(() => import('../pages/member/OrderJersey'))
const Parent = lazy(() => import('../pages/member/Parent'))
const About = lazy(() => import('../pages/member/About'))
const memberRoutes = [
    {
        path: '/',
        component: Home,
    },
    {
        path: '/event',
        component: Event,
    },
    {
        path: '/event/:id',
        component: DetailEvent,
    },
    {
        path: '/event/registration/:id',
        component: RegistrationEvent,
    },
    {
        path: '/member-card',
        component: MemberCard
    },
    {
        path: '/setting',
        component: Setting
    },
    {
        path: '/setting/account',
        component: Account
    },
    {
        path: '/setting/riders',
        component: Riders
    },
    {
        path: '/setting/riders/:id',
        component: RiderDetail
    },
    {
        path: '/setting/riders/add-rider/:id',
        component: RegistrationMember
    },
    {
        path: '/setting/riders/order-jersey/:id',
        component: OrderJersey
    },
    {
        path: '/setting/parent',
        component: Parent
    },
    {
        path: '/about',
        component: About
    }
];

export default memberRoutes;
